import { Component,OnInit ,Input, NgModule} from '@angular/core';
import { HomeComponent } from './home/home.component';
import { HttpClient } from '@angular/common/http';
import { Router,Event, NavigationStart, NavigationEnd, NavigationError } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { TiffinService } from './tiffin.service';
import { LoadingBarService } from '@ngx-loading-bar/core';
import { Platform } from '@angular/cdk/platform';
import { LocationStrategy } from '@angular/common';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  cols;
  constructor(  private locationStrategy: LocationStrategy, private http: HttpClient , private router: Router,private cookie:CookieService,private TService:TiffinService,private loadingBar: LoadingBarService) {  
  }  

  title = 'SSNTiffinvala';
  uri=this.TService.uri;
  disuser(){
    if(!this.cookie.get("refreshtoken")){
      this.router.navigate(['/Login'])
    }
    else{
      this.getuser();
      this.getorders()
    }
  }
  getuser(){
    var val;
    this.http.get(`${this.uri}/getdata`)  
    .subscribe( function(res){
      val=res;
    }); 
    setTimeout(() => {
      if(val.result){
        window.localStorage.setItem('user', JSON.stringify(val.Username))
        window.localStorage.setItem('refreshtoken',val.refreshtoken)
        window.localStorage.setItem('accesstoken',val.accesstoken)
      }
      this.TService.handlestatus(val.status)
    }, 3500);
  }
  checkuser(){
    var val;
    this.http.get(`${this.uri}/checkuser`)
    .subscribe( function(res){
      val=res;
    }); 
    setTimeout(() => {
      if(val.status){
        this.TService.handlestatus(val.status);
      }
    }, 2000);
  }
  preventBackButton() {
    history.pushState(null, null, location.href);
    this.locationStrategy.onPopState(() => {
      history.pushState(null, null, location.href);
    })
  }
  getorders(){
    var val
    this.http.get(`${this.uri}/userOrders`)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => {
        if(val.result){
          if(val.data){
            var orders=val.data
            var Corders=val.data.filter(order => order.Date === this.TService.date || order.Date ===this.TService.Todaydate);
            window.localStorage.setItem('orders',JSON.stringify(orders))
            window.localStorage.setItem('Corders',JSON.stringify(Corders))

          }
          else{
            window.localStorage.setItem('orders',JSON.stringify([]))
            window.localStorage.setItem('Corders',JSON.stringify([]))
          }
        }
        else{

        }
        // this.TService.handlestatus(val.status);
      }, 2000);
  }

  ngOnInit(): void {
    this.checkuser();
    this.preventBackButton();
    this.disuser();
  }
}
