import { HttpRequest, HttpHandler, HttpInterceptor, HttpEvent } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
    constructor(private cookie:CookieService){}
    intercept(req: HttpRequest<any>,
              next: HttpHandler): Observable<HttpEvent<any>> {
        // const idToken = localStorage.getItem("id_token");const idToken
        if(window.localStorage.getItem('refreshtoken')!=null){
        const idToken = window.localStorage.getItem('refreshtoken');
        if (idToken) {
            const cloned = req.clone({
                headers: req.headers.set("Authorization",
                      idToken)
            });

            return next.handle(cloned);
        }
        else {
            return next.handle(req);
        }
        }

        else if(this.cookie.check("refreshtoken")){
            const idToken=this.cookie.get("refreshtoken")
            if (idToken) {
                const cloned = req.clone({
                    headers: req.headers.set("Authorization",
                          idToken)
                });
    
                return next.handle(cloned);
            }
            else {
                return next.handle(req);
            }
        }
        else{
            return next.handle(req);
        }

       

        
    }
}
