import { Component, OnInit, Input, NgModule } from '@angular/core';  
import { ActivatedRoute, Router } from '@angular/router'; 
import { HttpClient, HttpHeaderResponse,HttpHeaders } from '@angular/common/http';
import { HomeComponent } from "./../home/home.component";  
import { CookieService } from 'ngx-cookie-service';
import { TiffinService } from '../tiffin.service';
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
  sets=false;
  hide = true;
  constructor(  private http: HttpClient , private router: Router,private cookie:CookieService,private TService:TiffinService) {  
  }  
 

  uri = this.TService.uri; 
  Login(Username,Password) {  
    this.sets=true;
  var val;
  Username=Username.toLowerCase();
    const objs={
      Username,
      Password
    }
    
    this.http.post(`${this.uri}/Login`,objs)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => 
      {
        if(val.result){
          if(val.data){
        window.localStorage.setItem('user', JSON.stringify(val.Username))
        this.cookie.set("refreshtoken",val.token.refreshtoken,0.05);
        window.localStorage.setItem('refreshtoken',val.token.refreshtoken)
        window.localStorage.setItem('accesstoken',val.token.accesstoken)
        this.sets=false;
        this.router.navigate(['Home'])
        }
        else{
        this.sets=false;
        this.router.navigate(['Login'])
        }
      }
        else{
          if(val.errormessage){
            this.TService.snackbardisplay(val.errormessage);
            this.sets=false
            this.router.navigate(['Login'])
          }
        }
        this.TService.handlestatus(val.status)
      },
      2500);
  } 
  ngOnInit() :void{
    
  }

}
