import { Component, OnInit,Input,NgModule } from '@angular/core';
import { Router} from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { TiffinService } from '../tiffin.service';

@Component({
  selector: 'app-update-profile',
  templateUrl: './update-profile.component.html',
  styleUrls: ['./update-profile.component.css']
})

export class UpdateProfileComponent implements OnInit {
  sets=false;
  hide=true
  uri=this.TService.uri;
  constructor(private router: Router,private http:HttpClient,private cookie:CookieService ,private TService:TiffinService) { 
  }
  
  fullname;adress;zipcode;mobile_number;
  addUser(FullName,Adress,Zipcode,Mobile_Number) {  
    this.sets=true;
    var val;
    const objs={
      FullName,Adress,Zipcode,Mobile_Number
    }
    this.http.put(`${this.uri}/UpdateProfile`,objs)  
    .subscribe(function(res){
       val=res;
      }); 
      setTimeout(() => 
      {
        if(val.result){
          this.getpinfo();
          this.TService.snackbardisplay('Profile Information sucessfully Updated')
        this.router.navigate(['/Update'])
        this.sets=false
        }
        else{
          if(val.errormessage){ this.TService.snackbardisplay(val.errormessage)}
          this.sets=false
        }
      // this.TService.handlestatus(val.status)
      },
      2500);
  }
  backtoHome(){
    this.sets=true;
    setTimeout(() => {
      this.router.navigate(['/Home']);
      this.sets=false      
    }, 1000);
  }
  changepassword(currpass,newpass,confpass){
    this.sets=true;
    var val;
    if(newpass===confpass){
      var obj={currpass,newpass}
      this.http.post(`${this.uri}/changpassword`,obj)  
    .subscribe(function(res){
       val=res;
      });
      setTimeout(() => {
        if(val.result){
          window.localStorage.clear();
          this.cookie.deleteAll();
          this.TService.snackbardisplay('Please login agian using new Password');
          this.sets=false
          this.router.navigate(['/Login']);
        }
        else{
          this.TService.snackbardisplay('please try after some time or check your current password');
          this.sets=false
        }
        this.TService.handlestatus(val.status)
      }, 2500);
    }
    else{
      this.TService.snackbardisplay('New Password and Confirm password not Matching')
      this.sets=false
    }

  }
  getpinfo(){
     this.sets=false;
    var val;
    this.http.get(`${this.uri}/getuserpersonalinfo`)  
    .subscribe(function(res){
       val=res;
      });
      setTimeout(() => {
        if(val.result){
          this.fullname=val.FullName;
          this.adress=val.Adress;
          this.zipcode=val.Zipcode;
          this.mobile_number=val.Mobile_Number
          this.sets=false;
        }
        this.TService.handlestatus(val.status)
      }, 2500); 
  }
  ngOnInit(): void {
    this.sets=true
    this.getpinfo();
  }

}
