import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { TiffinService } from '../tiffin.service';

@Component({
  selector: 'app-chefinfo',
  templateUrl: './chefinfo.component.html',
  styleUrls: ['./chefinfo.component.css']
})
// chef[0]image="https://material.angular.io/assets/img/examples/shiba2.jpg"

export class ChefinfoComponent implements OnInit {
  sets=false;
  chef;
  orders;
  Corders;
   date=this.TService.date
   Todaydate=this.TService.Todaydate

  Total;
  Session;
  expansion2=false;
 
  constructor(private cookie:CookieService,private router:Router,private http:HttpClient,private TService:TiffinService) { }
  setdis(){
   this.expansion2=!this.expansion2;
   this.Total=parseInt(this.chef[0].Cost);
  }
  uri=this.TService.uri
  registerorder(Adress,Mobile,Session,Note){
    this.sets=true
    if(!Adress&&!Mobile){
      this.TService.snackbardisplay('please add Adress , Mobile Number and Time of Tiffin');
      this.sets=false
    }
    else if(!Adress){
      this.TService.snackbardisplay('Please Add Your Current Adress')
      this.sets=false

    }
    else if(!Mobile){
      this.TService.snackbardisplay('Please enter your working mobile number')
      this.sets=false

    }
    else if(!Session){
      // this.TService.snackbardisplay('Please choose your time for Tiffin');
      Session=this.chef[0].Session
    }
    if(Session=='LunchAndDinner'){
      this.Total=this.Total*2
      this.TService.snackbardisplay('you are selected Lunch And Dinner option so price will be doubles')
    }
    else{
      this.sets=true
    console.log('i got')
    this.sets=true
    var val;
    var ChefUserName=this.chef[0].Username;
    var Amount=this.Total
    var Date=this.date
    var Status='Pending'
    var ChefName=this.chef[0].Pname
    var ChefMenu=this.chef[0].Menu
    var Type=this.chef[0].TypeF
    var obj={
      ChefName,ChefUserName,ChefMenu,Date,Type,Session,Adress,Mobile,Amount,Note,Status
    }
    console.log(obj)
    this.http.post(`${this.uri}/NewOrder`,obj)
    .subscribe(Response=>{
         val=Response;
         console.log(Response)
      }) 
      setTimeout(() => {
        if(val.result){
          this.TService.snackbardisplay('Order placed Sucessfully')
          this.getorders();
        }
        else{
          this.TService.snackbardisplay('problem')
        }
        this.sets=false
        this.TService.handlestatus(val.status)
        this.sets=false
      }, 3500);
  }
}

  backtoHome(){
    this.sets=true;
    setTimeout(() => {
      this.cookie.delete("cchef")
      this.router.navigate(['/Home']);
      this.sets=false      
    }, 1000);
  }

  delete(index){
    this.sets=true
    var val;
    // //console.log(index);
    var obj=this.orders[index]
    obj.Status='Canceled'
    this.http.put(`${this.uri}/UpdateOrder`,obj)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => {
        if(val.result){
          this.TService.snackbardisplay('Order sucessfully canceled');
          this.getorders();
          this.sets=false
        }
        else{
          this.TService.snackbardisplay('Try again after some time');
          this.sets=false
        }
        this.TService.handlestatus(val.status);
      }, 3000);
  }

  getorders(){
    this.sets=true
    var val
    this.http.get(`${this.uri}/userOrders`)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => {
        if(val.result){
          if(val.data){
            this.orders=val.data
            window.localStorage.setItem('orders',JSON.stringify(this.orders))
            this.Corders=val.data.filter(order => order.Date === this.date || order.Date ===this.Todaydate);
            window.localStorage.setItem('Corders',JSON.stringify(this.Corders))
            // //console.log(this.orders);
            // this.router.navigate(['/ChefInfo'])
            this.sets=false
          }
          else{
            this.sets=false
          }
        }
        else{
          this.sets=false
        }
        this.TService.handlestatus(val.status);
      }, 3000);
  }




  ngOnInit(): void {
    // this.getorders();
    if(window.localStorage.getItem('orders')||window.localStorage.getItem('Corders')){
    this.Corders=JSON.parse(window.localStorage.getItem('Corders'))
    this.orders=JSON.parse(window.localStorage.getItem('orders'))}
    else{
      this.getorders();
    }
    if(JSON.parse(window.localStorage.getItem("customzip"))){
      // //console.log('log')
      this.chef=JSON.parse(window.localStorage.getItem("chefwithcustomzip")).filter(chef => chef.Username ===JSON.parse(this.cookie.get("cchef")))
      // //console.log(this.chef)
    }
    else{
     try{
       var chefs=JSON.parse(window.localStorage.getItem("chefs"))
       this.chef=chefs.filter(chef => chef.Username ===JSON.parse(this.cookie.get("cchef")))
    }

     catch{
      //  //console.log('problem')
     }
    }

  }

}
