import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class TiffinService {

  constructor(private router:Router,private cookie:CookieService,private snackBar:MatSnackBar) { }
  dat: Date = new Date();  
  tda=this.dat.getDate().toString()
  tmonth=this.dat.getMonth().toString()
  tyear=this.dat.getFullYear().toString()
 Todaydate=this.tda+'/'+this.tmonth+'/'+this.tyear;
  currentDate = new Date(new Date().getTime() + 24 * 60 * 60 * 1000);
  year=parseInt(this.currentDate.getFullYear().toString())
  da=parseInt(this.currentDate.getDate().toString())
  month=parseInt(this.currentDate.getMonth().toString())
  date=this.da+'/'+this.month+'/'+this.year;
  // uri='http://localhost:4000'
  uri='https://tiffinvilla.herokuapp.com'

  handlestatus(status){
    if(status==401 || status==403){
      this.cookie.deleteAll();
      window.localStorage.clear();
      this.snackbardisplay('Your session has been expiered! Please login again')
      this.router.navigate(['/Login'])
    }
    else if(status==200||status==201){
      // //console.log('ok');
    }
    else if(status==203){
     this.snackbardisplay('please check credentials');
     
    } else if(status==303){
      this.snackbardisplay('Orders not found');
     }
    else{
      // //console.log('ok');
    }
  }
  snackbardisplay(message){
      this.snackBar.open(message,'okay',{ verticalPosition:'top',panelClass: 'happy'});
  }
  
 
    
}
