import { Component, OnInit,Input} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'; 
import { HttpClient, HttpHeaderResponse } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { TiffinService } from '../tiffin.service';
import { Platform } from '@angular/cdk/platform';
import { ChefinfoComponent } from '../chefinfo/chefinfo.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  uri=this.TService.uri ; 
  col;
    constructor( private platform:Platform, private http: HttpClient , private router: Router,private cookie:CookieService ,private TService:TiffinService) {  
  }  
  date=this.TService.date;
  sets=false;
  chefs;
  orders=[];
  customzip=false;
  zipcode;
  getchefswithzipcode(Zipcode){
    this.sets=true;
    var val;
    var obj={Zipcode}
    this.http.post(`${this.uri}/chefzip`,obj)  
    .subscribe( function(res){
      val=res;
    }); 
    setTimeout(() => {
      if(val.result){
        try{
        if(val.data){
          this.zipcode=Zipcode;
          this.customzip=true;
          this.chefs=val.users
          this.chefs=this.chefs.filter(chef => chef.Date ===this.date)
           window.localStorage.setItem('chefwithcustomzip',JSON.stringify(val.users));
           window.localStorage.setItem('customzip',JSON.stringify(true));
        }
        else{
          this.customzip=false;
          //console.log('ok')
          this.chefs=JSON.parse(window.localStorage.getItem('chefs'));
          window.localStorage.setItem('customzip',JSON.stringify(false));
          window.localStorage.setItem('chefwithcustomzip',JSON.stringify([]));
          this.TService.snackbardisplay('There is no chefs Loading loacal host chefs') 
        }
       
      }
        catch{
          this.TService.snackbardisplay('Welcome to TiffinVilla Family!');
        }
        this.router.navigate(['/Home'])
        this.sets=false;
      }
      else{
        // this.TService.snackbardisplay('Welcome to TiffinVilla Family!');
       
        this.sets=false
      }
      // this.TService.handlestatus(val.status)
    },3000);
  }

  getmoreinfo(Username){
    // //console.log(Username) 
    this.cookie.set("cchef",JSON.stringify(Username),0.02)
    this.sets=true
    setTimeout(() => {
      this.sets=false
      this.router.navigate(['/ChefInfo'])      
    }, 1000);
  }

  getchefs(){
    this.sets=true;
    var val;
    this.http.get(`${this.uri}/getchefs`)  
    .subscribe( function(res){
      val=res;
    }); 
    setTimeout(() => {
      if(val.result){
        try{if(val.data){
           window.localStorage.setItem('chefs',JSON.stringify(val.users));
           this.chefs=val.users;
           this.chefs=this.chefs.filter(chef => chef.Date ===this.date)
            window.localStorage.setItem('customzip',JSON.stringify(false));
        }
        else{
          this.chefs=[];
          window.localStorage.setItem('chefs',JSON.stringify([]));
          this.TService.snackbardisplay('There is no working chefs in your area!!')
        }
      }
        catch{
          this.TService.snackbardisplay('Welcome to TiffinVilla Family!');
        }
        this.router.navigate(['/Home'])
        this.sets=false;
      }
      else{
        this.TService.snackbardisplay('Welcome to TiffinVilla Family!');
        this.sets=false
        
      }
      this.TService.handlestatus(val.status)
    },3500);
  }

  getgridsys(){
    if(this.platform.ANDROID||this.platform.IOS){
      this.col=1
    }
    else{
      this.col=2
    }
  }

  ngOnInit(): void {
    this.getgridsys();
    this.sets=true;
    if(!window.localStorage.getItem('refreshtoken')){
      this.sets=false
      this.router.navigate(['/Login'])
    }
    if(this.customzip){
      this.chefs=JSON.parse(window.localStorage.getItem("chefwithcustomzip"));
      this.chefs=this.chefs.filter(chef => chef.Date ===this.date)
    }
    else{
    if(!window.localStorage.getItem('chefs')){
      //console.log('got')
      this.getchefs();
    }
    else{
      setTimeout(() => {
        this.chefs=JSON.parse(window.localStorage.getItem('chefs'));
        this.chefs=this.chefs.filter(chef => chef.Date ===this.date)
        this.sets=false
      }, 1500);
    }
    setTimeout(() => {
      if(this.chefs.length==0){
        this.TService.snackbardisplay('There is no chefs avalible today');
      }
    }, 3500);
    }
    
  // this.cic.getorders();    
  }

}
