import { Component, OnInit,Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { TiffinService } from '../tiffin.service';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
  sets=false;
  uri=this.TService.uri
  Corders;
  orders: any;
  constructor(private http:HttpClient,private router:Router,private TService:TiffinService) { }
  date=this.TService.date
  Todaydate=this.TService.Todaydate
  getorders(){
    this.sets=true
    var val
    this.http.get(`${this.uri}/userOrders`)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => {
        if(val.result){
          if(val.data){
            this.orders=val.data;
            this.Corders=val.data.filter(order => order.Date === this.date || order.Date ===this.Todaydate);
            window.localStorage.setItem('orders',JSON.stringify(this.orders))
            window.localStorage.setItem('Corders',JSON.stringify(this.Corders))
            this.sets=false
          }
          else{
            this.sets=false
            this.orders=[]
            this.Corders=[]
            window.localStorage.setItem('orders',JSON.stringify([]))
            window.localStorage.setItem('Corders',JSON.stringify([]))
          }
          this.router.navigate(['/Orders'])
        }
        else{
          this.sets=false
        }
        this.TService.handlestatus(val.status);
      }, 3000);
  }

  delete(index){
    this.sets=true
    var val;
    // //console.log(index);
    var obj=this.orders[index]
    obj.Status='Canceled'
    this.http.put(`${this.uri}/UpdateOrder`,obj)
    .subscribe(Response=>{
         val=Response;
      }) 
      setTimeout(() => {
        if(val.result){
          this.TService.snackbardisplay('Order sucessfully canceled');
          this.getorders();
          this.sets=false
        }
        else{
          this.TService.snackbardisplay('Try again after some time');
          this.sets=false
        }
        this.TService.handlestatus(val.status);
      }, 3000);
  }
  backtoHome(){
    this.sets=true;
    setTimeout(() => {
      this.router.navigate(['/Home']);
      this.sets=false      
    }, 1000);
  }

  ngOnInit(): void {
    if(window.localStorage.getItem('orders')){
      this.orders=JSON.parse(window.localStorage.getItem('orders'))
      this.Corders=JSON.parse(window.localStorage.getItem('Corders'))
    }
    else{
      this.getorders();
    }
  }

}
