import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'; 
import { HttpClient } from '@angular/common/http';
import { TiffinService } from '../tiffin.service';

@Component({
  selector: 'app-registation',
  templateUrl: './registation.component.html',
  styleUrls: ['./registation.component.css']
})
export class RegistationComponent implements OnInit {
  sets=false;
  hide=true;
  gender;
  constructor(private router: Router,private http:HttpClient,private TService:TiffinService) {  
  }  
  uri=this.TService.uri;
  addUser(FullName,Adress,Zipcode,Mobile_Number,gender ,dob,Username,Password) {  
    this.sets=true;
    var val;
  Username=Username.toLowerCase();
    const objs={
      FullName,Adress,Zipcode,Mobile_Number,gender,dob,Username,Password
    }
    // //console.log(objs);
    this.http.post(`${this.uri}/Register`,objs)  
    .subscribe(function(res){
       val=res;
      }); 
      setTimeout(() => 
      {
        if(val.result){
        this.TService.snackbardisplay('Please login using Username and Password')
        this.sets=false;
        this.router.navigate(['/Login'])
        } 
        else{
          if(val.errormessage){
            this.TService.snackbardisplay(val.errormessage);
            this.router.navigate(['/Login'])
          }
          else{
          this.TService.snackbardisplay('Please try again Some time')
          this.sets=false
          this.router.navigate(['/Register'])}
        }
        this.TService.handlestatus(val.status)
       } ,3500);
  } 
  ngOnInit(): void {
    // this.snackbar.open('hello','ok');
  }

}
