import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { TiffinService } from '../tiffin.service';
@Component({
  selector: 'app-header-loggedin',
  templateUrl: './header-loggedin.component.html',
  styleUrls: ['./header-loggedin.component.css']
})
export class HeaderLoggedinComponent implements OnInit {
  uri = this.TService.uri; 
  user;
  date=this.TService.date
  Todaydate=this.TService.Todaydate
  sets=false;
    constructor(  private http: HttpClient , private router: Router,private cookie:CookieService,private TService:TiffinService ) {  
  }  
  setuser(){
    this.user=window.localStorage.getItem('user')
    this.user = this.user.substring(1, this.user.length-1);
  }
  updateprofile(){
    this.sets=true;
    setTimeout(() => {
      this.router.navigate(['/Update'])      
      this.sets=false;
    }, 1000);
  }
  Orders(){
      this.sets=true
      var val
      this.http.get(`${this.uri}/UserOrders`)
      .subscribe(Response=>{
           val=Response;
           //console.log(val)
        }) 
        setTimeout(() => {
          if(val.result){
            if(val.data){
              var orders=val.data;
              var Corders=val.data.filter(order => order.Date === this.date || order.Date ===this.Todaydate);
              window.localStorage.setItem('orders',JSON.stringify(orders))
              window.localStorage.setItem('Corders',JSON.stringify(Corders))
              this.sets=false
            }
            else{
              this.sets=false
              window.localStorage.setItem('orders',JSON.stringify([]))
              window.localStorage.setItem('Corders',JSON.stringify([]))
            }
            //console.log('done')
            this.router.navigate(['/Orders'])      

          }
          else{
            this.sets=false
          }
          this.TService.handlestatus(val.status);
        }, 4000);
  }
  logout(){
    this.sets=true;
    var val;
    this.http.delete(`${this.uri}/Logout`)  
    .subscribe( function(res){
      val=res;
    }); 
    setTimeout(() => {
      if(val.result){
        window.localStorage.clear();
        this.cookie.deleteAll();
        this.sets=false;
        this.router.navigate(['/Login'])
      }
      this.TService.handlestatus(val.status);
    }, 3000);
  }
  
  ngOnInit(): void {
    this.setuser();
  }

}
